"use client";

import Link from "next/link";
import { AppShell } from "@/components/layout/app-shell";
import { HeroSection } from "@/components/layout/hero-section";
import { GlassCard } from "@/components/layout/glass-card";
import { useThemeStore } from "@/stores/theme-store";

export default function HomePage() {
  const resolved = useThemeStore((s) => s.resolved);
  return (
    <AppShell activeTab="/dashboards">
      <div className="grid gap-6 lg:grid-cols-[1fr_320px]">
        <HeroSection
          title="Welcome back"
          subtitle="Control your home in one place."
          backgroundImage={resolved}
        />
        <aside className="flex flex-col gap-4">
          <GlassCard>
            <h3 className="text-card-title font-medium text-gray-700 dark:text-gray-200">
              Energy Usage
            </h3>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">74% • Medium</p>
            <div className="mt-3 h-2 w-full overflow-hidden rounded-full bg-gray-200 dark:bg-white/10">
              <div
                className="h-full rounded-full bg-accent-yellow dark:bg-accent-green"
                style={{ width: "74%" }}
              />
            </div>
          </GlassCard>
          <GlassCard>
            <h3 className="text-card-title font-medium text-gray-700 dark:text-gray-200">
              Quick start
            </h3>
            <p className="mt-2 text-sm text-gray-600 dark:text-gray-300">
              Set up your dashboard in 5–10 minutes with live Home Assistant data.
            </p>
            <Link
              href="/onboarding"
              className="mt-3 inline-block rounded-full bg-gray-900 px-4 py-2 text-sm font-medium text-white transition hover:bg-gray-800 dark:bg-accent-green dark:text-gray-900 hover:dark:bg-accent-green/90"
            >
            Start onboarding
            </Link>
          </GlassCard>
        </aside>
      </div>
    </AppShell>
  );
}
