"use client";

import { cn } from "@/lib/utils";
import { Sidebar } from "./sidebar";
import { FloatingToolbar } from "./floating-toolbar";

type AppShellProps = {
  children: React.ReactNode;
  activeTab?: string;
  showSidebar?: boolean;
  showFloatingToolbar?: boolean;
  className?: string;
};

export function AppShell({
  children,
  activeTab = "/dashboards",
  showSidebar = true,
  showFloatingToolbar = false,
  className,
}: AppShellProps) {
  return (
    <div className={cn("flex min-h-screen flex-col bg-page-light dark:bg-dark-page", className)}>
      <div className="flex flex-1 overflow-hidden">
        {showSidebar && (
          <div className="flex shrink-0 flex-col items-center py-4 pl-3">
            <Sidebar activeHref={activeTab} />
          </div>
        )}
        <main className="flex-1 overflow-auto p-4">{children}</main>
      </div>

      {showFloatingToolbar && <FloatingToolbar />}
    </div>
  );
}
